@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-display: swap;
}
html {
  height: 100%;
}
body {
  margin: 0;
  display: flex;
  flex-direction: column;
  min-height: 100%;
}
.main-container {
  flex-grow: 1;
}
a {
  text-decoration: none;
}
.page-wrapper {
  width: 280px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .page-wrapper {
    width: 709px;
  }
}
@media (min-width: 1440px) {
  .page-wrapper {
    width: 1220px;
  }
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.reset-list {
  margin: 0;
  padding-left: 0;
  list-style-type: none;
}
nav {
  outline: 1px solid rgba(255, 0, 0, 0.8);
  outline-offset: -1px;
}
ul {
  outline: 2px solid rgba(8, 172, 30, 0.8);
  outline-offset: -2px;
}
li {
  outline: 1px dotted rgba(193, 243, 13, 0.8);
  outline-offset: -1px;
}
section {
  outline: 1px solid rgba(132, 13, 243, 0.8);
  outline-offset: -1px;
}
.slider {
  outline: 5px dotted rgba(13, 178, 243, 0.8);
  outline-offset: -5px;
}
.header {
  display: block;
  margin: 0;
  min-height: 65px;
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
  color: #000000;
}
.button-catalog {
  display: block;
  margin: 0;
  width: 100%;
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  text-align: center;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #68b738;
  border: none;
}
@media (min-width: 768px) {
  .main-nav__inner {
    display: flex;
    justify-content: space-between;
  }
}
.main-nav__logo-list {
  display: flex;
  justify-content: space-between;
  min-height: 66px;
}
@media (min-width: 768px) {
  .main-nav__menu-list {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 440px;
  }
}
@media (min-width: 1440px) {
  .main-nav__menu-list {
    width: 478px;
  }
}
.main-nav__menu-item {
  min-height: 66px;
}
.main-nav__link {
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  text-align: center;
  color: #000000;
}
@media (min-width: 768px) {
  .main-nav__link {
    font-size: 18px;
  }
}
@media (min-width: 1440px) {
  .main-nav__link {
    line-height: 30px;
  }
}
@media (min-width: 1440px) {
  .main-nav__link--index {
    color: #ffffff;
  }
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
.banner {
  padding-top: 27px;
}
.banner__header {
  margin-bottom: 25px;
  line-height: 36px;
  color: #ffffff;
}
@media (min-width: 768px) {
  .banner__header {
    width: 414px;
    font-size: 60px;
    line-height: 60px;
    color: #000000;
  }
}
@media (min-width: 768px) {
  .banner__inner {
    display: flex;
    flex-direction: column;
  }
}
@media (min-width: 1440px) {
  .banner__inner {
    min-height: 560px;
    margin-bottom: 82px;
  }
}
.banner__inner::before {
  position: absolute;
  top: 265px;
  left: 0;
  z-index: -1;
  display: block;
  content: "";
  width: 100%;
  height: 302px;
  background-color: #68b738d9;
}
@media (min-width: 768px) {
  .banner__inner::before {
    display: none;
  }
}
@media (min-width: 1440px) {
  .banner__inner::before {
    top: 0;
    left: 50%;
    display: block;
    width: 50%;
    min-height: 696px;
  }
}
.banner__inner::after {
  position: absolute;
  top: 265px;
  left: 0;
  z-index: -1;
  display: block;
  content: "";
  width: 100%;
  height: 302px;
  background-image: linear-gradient(#68b738d9, #68b738d9), url("../img/index/index-background-mobile.jpg");
  background-repeat: no-repeat;
  background-position: center;
}
@media (min-width: 768px) {
  .banner__inner::after {
    display: none;
  }
}
@media (min-width: 1440px) {
  .banner__inner::after {
    top: 0;
    left: 50%;
    display: block;
    width: 50%;
    min-height: 696px;
  }
}
.banner__text {
  margin: 0;
  margin-bottom: 29px;
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 14px;
  text-transform: uppercase;
  text-align: center;
  color: #ffffff;
}
@media (min-width: 768px) {
  .banner__text {
    font-size: 20px;
    line-height: 20px;
    text-align: left;
    color: #000000;
  }
}
@media (min-width: 768px) {
  .banner__image {
    order: 10;
    width: 709px;
    height: 609px;
  }
}
@media (min-width: 1440px) {
  .banner__image {
    position: absolute;
    width: 552px;
    height: 500px;
    top: 20%;
    left: 40%;
  }
}
@media (min-width: 768px) {
  .banner__button {
    width: 200px;
    word-wrap: break-word;
    padding: 14px 26px;
  }
}
.categories {
  margin-bottom: 20px;
}
@media (min-width: 768px) {
  .categories {
    margin-bottom: 15px;
  }
}
.categories__list {
  display: flex;
  flex-wrap: wrap;
}
@media (min-width: 1440px) {
  .categories__list {
    justify-content: space-between;
  }
}
.categories__item {
  width: 100%;
  background-color: #F2F2F2;
}
@media (min-width: 768px) {
  .categories__item {
    margin-bottom: 30px;
    padding: 77px 62px 53px 52px;
  }
}
@media (min-width: 1440px) {
  .categories__item {
    width: 456px;
  }
}
.product-line__header {
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 37px;
  text-transform: uppercase;
  color: #000000;
}
.product-line__text {
  font-family: "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
}
@media (min-width: 768px) {
  .product-line__text {
    width: 340px;
  }
}
.product-line__link {
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  text-align: center;
  text-transform: uppercase;
  color: #000000;
}
.features {
  margin-bottom: 30px;
}
@media (min-width: 768px) {
  .features {
    margin-bottom: 43px;
  }
}
@media (min-width: 768px) {
  .features__list {
    display: flex;
    flex-wrap: wrap;
  }
}
@media (min-width: 1440px) {
  .features__list {
    justify-content: space-between;
  }
}
@media (min-width: 768px) {
  .features__item {
    width: 246px;
    min-height: 280px;
    margin-right: 80px;
    margin-bottom: 37px;
  }
}
@media (min-width: 1440px) {
  .features__item {
    width: 230px;
    margin-right: 0;
  }
}
.features__text {
  margin: 0;
  font-family: "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
}
.demo {
  position: relative;
}
.demo::before {
  position: absolute;
  z-index: -1;
  content: "";
  width: 100%;
  height: 100%;
  bottom: 0;
  left: 0;
  background-color: #eaeaea;
}
@media (min-width: 768px) {
  .demo::before {
    height: 462px;
  }
}
@media (min-width: 1440px) {
  .demo::before {
    background-color: #F2F2F2;
  }
}
@media (min-width: 1440px) {
  .demo__inner {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: auto auto;
  }
}
.demo__text-box {
  width: 100%;
}
@media (min-width: 768px) {
  .demo__text-box {
    background-color: #ffffff;
  }
}
@media (min-width: 1440px) {
  .demo__text-box {
    width: 436px;
    grid-column: 1/2;
    grid-row: 2/3;
    background-color: #F2F2F2;
  }
}
.demo__slider {
  margin: 0 auto;
}
@media (min-width: 1440px) {
  .demo__slider {
    grid-column: 2/3;
    grid-row: 1/3;
  }
}
.demo__block {
  display: flex;
  flex-direction: column-reverse;
}
.demo__expenses {
  display: flex;
}
.demo__data-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
@media (min-width: 1440px) {
  .demo__data-list {
    width: 336px;
  }
}
.demo__text {
  font-family: "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
}
.demo__block-description {
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 24px;
  color: #000000;
}
.demo__block-term {
  font-family: "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 100%;
  color: #444444;
}
.demo__expenses-term {
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  text-transform: uppercase;
  color: #000000;
}
.demo__expenses-description {
  margin: 0;
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  text-transform: uppercase;
  color: #000000;
}
.slider {
  position: relative;
  width: 280px;
  height: 277px;
  background-color: transparent;
}
@media (min-width: 768px) {
  .slider {
    width: 590px;
    height: 576px;
  }
}
.slider__item {
  position: absolute;
  width: 100%;
  height: 100%;
  margin: 0;
  background-color: transparent;
}
.slider__image {
  width: 100%;
  height: 100%;
}
.slider__toggle {
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  color: #000000;
  border: none;
  background-color: transparent;
}
.page-footer--dark {
  background-color: #F2F2F2;
}
.page-footer__links {
  background-color: #F2F2F2;
}
@media (min-width: 768px) {
  .page-footer__inner {
    display: flex;
    justify-content: space-between;
  }
}
.address {
  position: relative;
}
.address__inner {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
@media (min-width: 1440px) {
  .address__inner {
    position: absolute;
    top: 95px;
    width: 565px;
    column-gap: 87px;
    background-color: #ffffff;
  }
}
.address__legend {
  order: -1;
  width: 139px;
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  color: #111111;
}
@media (min-width: 768px) {
  .address__legend {
    font-size: 20px;
    line-height: 26px;
  }
}
@media (min-width: 1440px) {
  .address__legend {
    justify-self: end;
  }
}
.address__description {
  width: 139px;
  font-family: "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #444444;
}
@media (min-width: 768px) {
  .address__description {
    justify-self: end;
  }
}
@media (min-width: 1440px) {
  .address__description {
    justify-self: start;
  }
}
.address__map {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.address__map-wrapper {
  width: auto;
  height: 362px;
  text-align: center;
}
@media (min-width: 768px) {
  .address__map-wrapper {
    height: 400px;
  }
}
.social__list {
  display: flex;
  justify-content: center;
  padding: 22px 0;
}
.social__link {
  display: block;
  width: 28px;
  height: 22px;
  margin: 0 12px;
  fill: #EBEBEB;
  background-repeat: no-repeat;
}
