.copyright {
  padding-top: 22px;
  padding-bottom: 32px;
}
.copyright__link {
  display: block;
  font-family: "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  color: #444444;
