.product {
  display: grid;
  grid-template-columns: 2fr 1.1fr 0.9fr;
  grid-template-rows: auto auto auto;
}
@media (min-width: 768px) {
  .product {
    display: flex;
    flex-direction: column;
    width: 324px;
    min-height: 511px;
  }
}
@media (min-width: 1440px) {
  .product {
    width: 245px;
